.statistics-page {
  padding: 20px;
  padding-bottom: 80px;
}

.page-title {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 24px;
  text-align: center;
}

.stats-summary {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  margin-bottom: 24px;
}

.stat-card {
  background-color: var(--tg-theme-secondary-bg-color);
  border-radius: 12px;
  padding: 16px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.stat-title {
  font-size: 14px;
  color: var(--tg-theme-hint-color);
  margin-bottom: 8px;
}

.stat-value {
  font-size: 24px;
  font-weight: 700;
  color: var(--tg-theme-button-color);
}

.stat-section {
  background-color: var(--tg-theme-secondary-bg-color);
  border-radius: 12px;
  padding: 16px;
  margin-bottom: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.section-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 16px;
}

.category-stats {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.category-item {
  display: flex;
  align-items: center;
  gap: 12px;
}

.category-color {
  width: 16px;
  height: 16px;
  border-radius: 4px;
}

.category-name {
  flex: 1;
  font-size: 14px;
}

.category-count {
  font-weight: 600;
}

.frequency-stats {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.frequency-item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
}

.frequency-name {
  width: 100px;
  font-size: 14px;
}

.frequency-bar-container {
  flex: 1;
  height: 12px;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  overflow: hidden;
}

.frequency-bar {
  height: 100%;
  border-radius: 6px;
}

.frequency-count {
  font-weight: 600;
  min-width: 30px;
  text-align: right;
}

.recent-habit-item {
  display: flex;
  flex-direction: column;
  padding: 12px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  cursor: pointer;
  transition: opacity 0.2s ease;
}

.recent-habit-item:last-child {
  border-bottom: none;
}

.recent-habit-item:hover {
  opacity: 0.8;
}

.recent-habit-title {
  font-weight: 500;
  margin-bottom: 4px;
}

.recent-habit-date {
  font-size: 12px;
  color: var(--tg-theme-hint-color);
}

.loading-state, .error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 300px;
  text-align: center;
}

.loading-spinner {
  margin-bottom: 16px;
}

.error-state {
  color: #FF3B30;
}

.error-state .button {
  margin-top: 16px;
  padding: 8px 16px;
  background-color: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

/* Categories Chart */
.categories-chart {
  margin-top: 15px;
}

.category-bars {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.category-bar-container {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.category-name {
  font-size: 14px;
  font-weight: 500;
}

.category-bar-wrapper {
  height: 25px;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 12px;
  overflow: hidden;
  position: relative;
}

.category-bar {
  height: 100%;
  border-radius: 12px;
  transition: width 0.5s ease;
}

.category-count {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 12px;
  font-weight: 700;
  color: var(--tg-theme-text-color);
}

/* Weekly Activity Chart */
.weekly-activity-chart {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 150px;
  margin-top: 15px;
}

.activity-day {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
}

.activity-bar-container {
  width: 15px;
  height: 120px;
  display: flex;
  align-items: flex-end;
  margin-bottom: 5px;
}

.activity-bar {
  width: 100%;
  background-color: var(--tg-theme-button-color);
  border-radius: 5px 5px 0 0;
  transition: height 0.5s ease;
}

.day-label {
  font-size: 12px;
  color: var(--tg-theme-hint-color);
}

/* Monthly Progress Chart */
.monthly-progress-chart {
  display: flex;
  height: 150px;
  gap: 2px;
  margin-top: 15px;
  align-items: flex-end;
}

.progress-day {
  flex: 1;
  height: 120px;
  display: flex;
  align-items: flex-end;
}

.progress-bar {
  width: 100%;
  background-color: var(--tg-theme-button-color);
  border-radius: 2px 2px 0 0;
  transition: height 0.5s ease;
}

.chart-labels {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  color: var(--tg-theme-hint-color);
  margin-top: 5px;
}

/* Habits Stats List */
.habits-stats-list {
  margin-top: 10px;
}

.habit-stat-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  cursor: pointer;
}

.habit-stat-item:last-child {
  border-bottom: none;
}

.habit-info {
  flex: 1;
}

.habit-title {
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 4px;
}

.habit-category {
  font-size: 13px;
}

.habit-completion-rate {
  font-size: 18px;
  font-weight: 600;
  color: var(--tg-theme-button-color);
}

/* Empty States */
.empty-state,
.empty-chart,
.empty-list {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 0;
  color: var(--tg-theme-hint-color);
  text-align: center;
} 