.settings-page {
  padding: 20px;
  padding-bottom: 80px;
}

.page-title {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 24px;
  text-align: center;
}

.settings-section {
  background-color: var(--tg-theme-secondary-bg-color);
  border-radius: 12px;
  padding: 16px;
  margin-bottom: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.section-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 16px;
}

.setting-item {
  display: flex;
  flex-direction: column;
  padding: 12px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.setting-item:last-child {
  border-bottom: none;
}

.setting-item.no-border {
  border-bottom: none;
}

.setting-info {
  flex: 1;
}

.setting-label {
  margin-bottom: 8px;
}

.setting-label span:first-child {
  display: block;
  font-weight: 500;
  margin-bottom: 4px;
}

.setting-description {
  font-size: 14px;
  color: var(--tg-theme-hint-color);
}

.setting-control {
  display: flex;
  justify-content: flex-start;
}

/* Переключатель (toggle switch) */
.toggle-switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
}

.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 34px;
}

.toggle-slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .toggle-slider {
  background-color: var(--tg-theme-button-color);
}

input:focus + .toggle-slider {
  box-shadow: 0 0 1px var(--tg-theme-button-color);
}

input:checked + .toggle-slider:before {
  transform: translateX(26px);
}

/* Селект и поле ввода времени */
.language-select, .time-input {
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: var(--tg-theme-bg-color);
  color: var(--tg-theme-text-color);
  font-size: 14px;
  min-width: 120px;
}

/* Кнопка очистки данных */
.clear-data-button {
  padding: 8px 16px;
  background-color: #FF3B30;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
  transition: opacity 0.2s ease;
}

.clear-data-button:hover {
  opacity: 0.9;
}

/* Кнопка сохранения настроек */
.settings-actions {
  display: flex;
  justify-content: center;
  margin-bottom: 24px;
}

.save-button {
  padding: 12px 24px;
  background-color: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: opacity 0.2s ease, transform 0.1s ease;
}

.save-button:hover {
  opacity: 0.9;
}

.save-button:active {
  transform: scale(0.98);
}

/* Информация о приложении */
.app-info {
  text-align: center;
  color: var(--tg-theme-hint-color);
  font-size: 12px;
}

.app-version {
  margin-bottom: 4px;
}

@media (min-width: 768px) {
  .setting-item {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  
  .setting-label {
    flex: 1;
    margin-bottom: 0;
  }
} 