.habit-details-page {
  padding: 20px;
  padding-bottom: 80px;
}

.habit-details-header {
  margin-bottom: 24px;
  text-align: center;
}

.habit-details-header h1 {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 8px;
}

.habit-status {
  margin-top: 8px;
}

.status-badge {
  display: inline-block;
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 14px;
  font-weight: 500;
}

.status-badge.active {
  background-color: #4CAF50;
  color: white;
}

.status-badge.completed {
  background-color: #9E9E9E;
  color: white;
}

.habit-details-content {
  background-color: var(--tg-theme-secondary-bg-color);
  border-radius: 12px;
  padding: 16px;
  margin-bottom: 24px;
}

.detail-section {
  margin-bottom: 24px;
}

.detail-section:last-child {
  margin-bottom: 0;
}

.detail-section h2 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 12px;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.detail-item:last-child {
  border-bottom: none;
}

.detail-label {
  color: var(--tg-theme-hint-color);
}

.detail-value {
  font-weight: 500;
}

.habit-details-actions {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.button {
  padding: 12px 16px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  cursor: pointer;
  transition: opacity 0.2s ease, transform 0.1s ease;
  border: none;
}

.complete-button {
  background-color: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
}

.edit-button {
  background-color: var(--tg-theme-secondary-bg-color);
  color: var(--tg-theme-button-color);
  border: 1px solid var(--tg-theme-button-color);
}

.delete-button {
  background-color: #FF3B30;
  color: white;
}

.button:active {
  transform: scale(0.98);
}

.loading-state, .error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 300px;
  text-align: center;
}

.loading-spinner {
  margin-bottom: 16px;
}

.error-state {
  color: #FF3B30;
}

.error-state .button {
  margin-top: 16px;
}

.habit-header {
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.habit-title {
  margin: 0 0 5px 0;
  font-size: 24px;
  font-weight: 600;
}

.habit-category {
  margin: 0;
  color: var(--tg-theme-hint-color);
  font-size: 14px;
}

.habit-description {
  margin-bottom: 20px;
  line-height: 1.5;
}

.habit-meta {
  background-color: var(--tg-theme-secondary-bg-color);
  border-radius: 12px;
  padding: 15px;
  margin-bottom: 25px;
}

.meta-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.meta-item:last-child {
  margin-bottom: 0;
}

.meta-label {
  color: var(--tg-theme-hint-color);
  font-size: 14px;
}

.meta-value {
  font-weight: 500;
}

.section-title {
  font-size: 18px;
  font-weight: 600;
  margin: 25px 0 15px;
}

.habit-stats {
  margin-bottom: 25px;
}

.stats-container {
  display: flex;
  justify-content: space-between;
  background-color: var(--tg-theme-secondary-bg-color);
  border-radius: 12px;
  padding: 15px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
}

.stat-value {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 12px;
  color: var(--tg-theme-hint-color);
}

.habit-calendar {
  margin-bottom: 30px;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-gap: 8px;
}

.calendar-day {
  aspect-ratio: 1/1;
  border-radius: 8px;
  cursor: pointer;
  position: relative;
}

.calendar-day.completed {
  background-color: var(--tg-theme-button-color);
}

.calendar-day.missed {
  background-color: rgba(0, 0, 0, 0.1);
}

.action-buttons {
  display: flex;
  gap: 15px;
}

.action-button {
  flex: 1;
  padding: 12px;
  border-radius: var(--button-border-radius);
  border: none;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.1s ease;
}

.action-button:active {
  transform: scale(0.98);
}

.action-button.edit {
  background-color: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
}

.action-button.delete {
  background-color: #e74c3c;
  color: white;
} 