:root {
  --tg-theme-bg-color: #ffffff;
  --tg-theme-secondary-bg-color: #f0f0f0;
  --tg-theme-text-color: #000000;
  --tg-theme-hint-color: #8e8e93;
  --tg-theme-link-color: #037ee5;
  --tg-theme-button-color: #037ee5;
  --tg-theme-button-text-color: #ffffff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', 'Helvetica Neue', sans-serif;
  background-color: var(--tg-theme-bg-color);
  color: var(--tg-theme-text-color);
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.5;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  position: relative;
}

.app-content {
  flex: 1;
  padding-bottom: 60px; /* Пространство для нижней навигации */
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  line-height: 1.2;
  margin-bottom: 0.5rem;
}

a {
  color: var(--tg-theme-link-color);
  text-decoration: none;
}

button {
  cursor: pointer;
  font-family: inherit;
}

.button {
  display: inline-block;
  background-color: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
  padding: 0.75rem 1.25rem;
  border-radius: 8px;
  border: none;
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  cursor: pointer;
  transition: opacity 0.2s, transform 0.1s;
}

.button:hover {
  opacity: 0.9;
}

.button:active {
  transform: scale(0.98);
}

.mt-2 {
  margin-top: 0.5rem;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-top-color: var(--tg-theme-button-color);
  animation: spin 1s linear infinite;
  margin: 0 auto;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Утилиты для анимаций */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Темная тема */
@media (prefers-color-scheme: dark) {
  :root {
    --tg-theme-bg-color: #1c1c1e;
    --tg-theme-secondary-bg-color: #2c2c2e;
    --tg-theme-text-color: #ffffff;
    --tg-theme-hint-color: #8e8e93;
    --tg-theme-link-color: #0a84ff;
    --tg-theme-button-color: #0a84ff;
    --tg-theme-button-text-color: #ffffff;
  }
} 